.app-wrapper {
  display: grid;
  grid-template-areas:
    "h s"
    "n c"
    "f f";
  grid-template-rows: 80px 1fr 120px;
  grid-template-columns: 1fr 5fr;
  font-family: sans-serif;
  background: url(https://wallpaperaccess.com/full/3461470.jpg);
}

.header {
  grid-area: h;
  background: #fecf06;
  border: solid 1px black;
  padding-right: 10px;
}
.header img {
  width: 100px;
  padding: 10px;
  padding-left: 55px;
}

.sectionr {
  grid-area: s;
  background: #fecf06;
  border: solid 1px black;
  text-align: center;
  font-size: 55px;
  cursor: pointer;
  text-decoration: inherit;
  color: inherit;
}

.nav {
  grid-area: n;
  padding: 100px;
  padding-left: 20px;
  margin-bottom: 5px;
}
.nav img {
  width: 150px;
  cursor: pointer;
  border-radius: 10%;
}

.content {
  grid-area: c;
  width: 1080px;
  min-height: 900px;
  margin: 0 auto;
  text-align: center;
  background: white;
}
.content img {
  width: 1080px;
  vertical-align: bottom;
}

.contenttwo {
  grid-area: c;
  width: 1080px;
  min-height: 900px;
  margin: 0 auto;
  text-align: center;
  background: white;
  display: flex;
  justify-content: space-between;
}

.contenttwo img {
  width: 200px;
}

.contentthree {
  grid-area: c;
}

.contentfour {
  grid-area: c;
}

.footer {
  grid-area: f;
  background: #013668;
  border: solid 1px black;
  text-align: center;
  font-size: 100px;
  color: #fffefe;
  vertical-align: bottom;
}

.active-link img {
  opacity: 60%;
}

.items {
  padding: 50px;
  text-decoration: none;
}

.pants {
  color: inherit;
  text-decoration: none;
}

button {
  width: 200px;
  height: 50px;
  margin: 20px;
  border: 2px solid black;
  background: white;
  cursor: pointer;
  font-family: sans-serif;
  font-size: 20px;
  font-weight: bold;
}

p {
  color: black;
}

.selector {
  width: 200px;
  height: 50px;
  margin: 20px;
  border: 2px solid black;
  background: white;
  cursor: pointer;
  font-family: sans-serif;
  font-size: 20px;
  color: black;
}
